import Vue from 'vue'
import Router from 'vue-router'


// import home from '@/pages/home'
// import apiclouds from '@/pages/apiclouds'
// import mine from '@/pages/mine'
// import others from '@/pages/others'
// import detail from '@/pages/detail'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: {
        name: 'home',
      },
      // meta:{keepAlive:true}
    },
    {
      path: '/home',
      name: 'home',
      meta:{names:'home'},
      component: () => import('@/pages/home')
      // meta:{keepAlive:true}
    },
    {
      path: '/articles',
      name: 'articles',
      meta:{names:'articles',ismore:true},
      component: () => import('@/pages/articles')
      // meta:{keepAlive:true}
    },
    {
      path: '/apiclouds',
      name: 'apiclouds',
      meta:{names:'apiclouds'},
      component: () => import('@/pages/apiclouds')
    },
    {
      path: '/mine',
      name: 'mine',
      meta:{names:'mine'},
      component: () => import('@/pages/mine')
    },
    {
      path: '/others',
      name: 'others',
      meta:{names:'others'},
      component: () => import('@/pages/others')
    },
    {
      path: '/detail',
      name: 'detail',
      meta:{names:'articles'},
      component: () => import('@/pages/detail')
    },
    {
      path: '/mineset',
      name: 'mineset',
      meta:{names:'mine'},
      component: () => import('@/pages/set')
    },
    {
      path: '/admin',
      name: 'admin',
      meta:{names:'mine',ismore:true},
      component: () => import('@/pages/admin/home')
    },
    {
      path: '/admin_edit',
      name: 'admin_edit',
      meta:{names:'mine'},
      component: () => import('@/pages/admin/edit')
    },
    
  ]
})



