// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueResource from 'vue-resource'
import axios from 'axios'
// import vaxios from 'vue-axios'
import MintUI from 'mint-ui'

import 'mint-ui/lib/style.css';
import '@/assets/css/api.css';
import '../static/js/api.js';




Vue.config.productionTip = false;
Vue.prototype.isApp = true;//是否在APP上使用
Vue.prototype.ismine = true;//是否为自己FTP-true,公司FTP-false

Vue.use(router)
Vue.use(VueResource)
// Vue.use(Axios)
Vue.use(axios)
// Vue.use(vaxios)
Vue.use(MintUI)
// Vue.component('MintUI',MintUI);
Vue.prototype.MintUI = MintUI;

import popLists from '@/components/poplist.vue';
Vue.component('popList',popLists);

Vue.prototype.mineDomain = 'http://www.linjianping.cn';
Vue.prototype.companyDomain = 'http://ljp.cn7.iswweb.cn';
Vue.prototype.addLink = '/tp/index.php';
Vue.prototype.$axios = axios;
Vue.prototype.hosts = Vue.prototype.ismine?Vue.prototype.mineDomain:Vue.prototype.companyDomain;//用于图片展示
Vue.prototype.host = Vue.prototype.isApp?Vue.prototype.hosts+Vue.prototype.addLink:'/apis'+Vue.prototype.addLink;//用于接口请求数据

// 数据请求
Vue.prototype.requests = function({reqUrl:reqUrl,reqData:reqData,reqMethods:reqMethods,reqCache:reqCache,reqTimeout:reqTimeout,reqDataType:reqDataType,reqCharset:reqCharset,reqHeaders:reqHeaders,reqReport:reqReport,reqReturnAll:reqReturnAll,reqProxy:reqProxy,handles:handles}){
  // method: get,post,put,delete,head,options,trace,patch----请求方式
  // dataType: json,text----返回数据类型。
  // cache----是否缓存
  // timeout----超时时间，单位秒
  // charset----当响应头里面没有返回字符集编码时，使用此编码来解析数据
  // headers----设置请求头数据
  // report----是否实时返回上传文件进度
  // returnAll----是否需要返回所有 response 信息（包括响应头、消息体、状态码）
  // proxy----设置代理请求服务器。
  // handles----showLoading是否显示加载弹框，closeLoading是否关闭加载弹框，closeLoadTime延迟关闭时间loadingText加载文字
  if(handles){
    if(handles.showLoading){
      MintUI.Indicator.open({
        text: handles.loadingText?handles.loadingText:'加载中...',
        spinnerType: 'fading-circle'
      });
    }
  }else{
    MintUI.Indicator.open({
      text: '加载中...',
      spinnerType: 'fading-circle'
    });
  }
  var _this = this;
  
  return new Promise(function (resolve, reject){
    if(Vue.prototype.isApp){
      api.ajax({
        url: Vue.prototype.host+reqUrl,
        method:reqMethods?reqMethods:'get',
        data:reqData?reqData:'',
        cache: reqCache?reqCache:false,
        timeout:reqTimeout?reqTimeout:60,
        dataType:reqDataType?reqDataType:'json',
        charset:reqCharset?reqCharset:'utf-8',
        headers:reqHeaders?reqHeaders:'',
        report:reqReport?reqReport:false,
        returnAll:reqReturnAll?reqReturnAll:false,
        proxy:reqProxy?reqProxy:''
      }, function(ret, err) {
        // 关闭加载弹框操作
        Vue.prototype.closeLoading(handles);
        if (ret) {
          // 导出数据
          // api.alert({
          //     title: '成功',
          //     msg: JSON.stringify(ret),
          // }, function(ret, err) {
          
          // });
          resolve(ret);
        } else {
          MintUI.Toast({
              message: '加载失败！',
              position: 'middle',
              duration: 3000
          })
          api.alert({
              title: '失败',
              msg: JSON.stringify(err),
          }, function(ret, err) {
          });
          reject(err);
        }
        
      })
    }else{
      // 'http://www.linjianping.cn/tp/index.php'
      // _this.$http.get(_this.Hosts+reqUrl,{params:reqData}).then(function(res){
      //   // 关闭加载弹框操作
      //   Vue.prototype.closeLoading(handles);
      //   resolve(res.data);
      // })
      axios({url:Vue.prototype.host+reqUrl,method:'get'}, {params: reqData}).then(response => {
        // 请求成功
        Vue.prototype.closeLoading(handles);
        console.log(response);
        resolve(response.data);
      }).catch(error => {
        // 请求失败，
        Vue.prototype.closeLoading(handles);
        console.log(error);
      });
    }
  })
}

// 关闭加载弹框--用于请求数据的关闭
Vue.prototype.closeLoading = function(handles){
  if(handles){
    if(handles.closeLoading){
      if(handles.closeLoadTime){
        setTimeout(function(){
          MintUI.Indicator.close();
        },handles.closeLoadTime)
      }else{
        MintUI.Indicator.close();
      }
    }
  }else{
    // Indicator.close();
    setTimeout(function(){
      MintUI.Indicator.close();
    },600)
  }
}

// 显示加载弹框
Vue.prototype.showLoading = function(text){
  MintUI.Indicator.open({
    text: text?text:'加载中...',
    spinnerType: 'fading-circle'
  });
}
// 隐藏加载弹框
Vue.prototype.hideLoading = function(times){
  if(times){
    setTimeout(function(){
      MintUI.Indicator.close();
    },times)
  }else{
    MintUI.Indicator.close();
  }
}
// 显示消息弹框
Vue.prototype.showMsg = function({text:text,weizhi:weizhi,times:times}){
  if(Vue.prototype.isApp){
    api.toast({
      msg: text,
      duration: times?times:2000,
      location: weizhi?weizhi:'middle'
    });
  }else{
    MintUI.Toast({
      message: text,
      position: weizhi?weizhi:'middle',
      duration: times?times:2000
    })
  }
}

// 判断是否有网络
Vue.prototype.checkLine = function(){
  // unknown          未知
  // ethernet         以太网
  // wifi             wifi
  // 2g               2G网络
  // 3g               3G网络
  // 4g               4G网络
  // none             无网络
  return api.connectionType;
}

/* eslint-disable no-new */
if(Vue.prototype.isApp){
  window.apiready = function(){
    window.app = new Vue({
      el: '#app',
      router,
      components: { App },
      template: '<App/>'
    });
    
  }
}else{
  window.app = new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
  });
}




