<template>
    <div class="popBox">
        <mt-popup v-model="popStatus" position="bottom">
            <div class="popTitles" v-if="popDatas.title">
                <p>{{popDatas.title}}</p>
            </div>
            <mt-picker :slots="popDatas.slots" valueKey="2015-02" @change="changClassify"></mt-picker>
            <div class="popupBtn">
                <button @click="popCancel" class="popCancel" type="button">取消</button>
                <button @click="popConfirm" class="popConfirm" type="button">确定</button>
            </div>
        </mt-popup>
    </div>
</template>
<script>
export default {
    name: 'poplist',
    data(){
        return{
            curData:'',
            popStatus:''
        }
    },
    props: ['popDatas','popStatus'],
    methods:{
        changClassify:function(picker, values){
            this.curData = {curData:picker,curValue:values};
            this.$emit('popChange',{curData:picker,curValue:values})
        },
        popCancel:function(){
            this.popStatus = false;
            this.$emit('popCancel',this.popStatus);
        },
        popConfirm:function(){
            this.popStatus = false;
            this.$emit('popConfirm',this.curData);
        }
    }
    
}
</script>
<style>
    .mint-popup-bottom{
        width: 100%;
    }
    .popTitles{
        text-align: center;
        font-size: 16px;
        padding: 10px 0;
        border-bottom: 1px solid rgb(230, 230, 230);
    }
    .popupBtn{
        margin-top: 10px;
        border-top: 1px solid rgb(230, 230, 230);
        padding: 10px 16px;
    }
    .popupBtn button{
        display: inline-block;
        width: 40%;
        border-radius: 4px;
        line-height: 40px;
    }
    .popCancel{
        border: 1px solid rgb(230, 230, 230);
    }
    .popConfirm{
        border: none;
        background-color: rgb(18, 150, 219);
        color: white;
        float: right;
    }
</style>


