<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <div class="h_area" v-if="showH" :style="{height:heights+'px'}"></div>
    <!-- <keep-alive> -->
    <router-view></router-view>
    <!-- </keep-alive> -->
    <Footers :curIndex="curindex" :theight="heights"></Footers>
  </div>
</template>

<script>
import Footers from '@/components/footers.vue';
export default {
  name: 'App',
  data(){
    return{
      showH: false,
      heights: '',
      curindex: 1,
    }
  },
  components: {
    Footers:Footers
  },
  mounted(){
    this.isApp?this.heights = api.safeArea.top:'';
    
  },
  watch: {
    $route(to, from) {
        console.log(to);
        console.log(from);
        switch(to.meta.names) {
          case "home":
            this.curindex = 1;
            break;
          case "articles":
            this.curindex = 2;
            break;
          case "apiclouds":
            this.curindex = 3;
            break;
          case "others":
            this.curindex = 4;
            break;
          case "mine":
            this.curindex = 5;
            break;
          default:
            this.curindex = 1;
            
        } 
        if(to.path=="/home"||to.path=="/articles"||to.path=="/mine"){
          this.showH = false;
        }else{
          this.showH = true;
        }
        if(!to.meta.ismore){
          this.isApp?this.removeEListener():'';
        }
    }
  },
  methods:{
    removeEListener(){
      api.removeEventListener({
        name: 'scrolltobottom'
      });
    }
  }
}
</script>

<style>

  #app {
    /* font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px; */
    padding-bottom: 50px;
  }
  .h_area{
    /* height:  */
  }
</style>



